

import { useEffect, useState } from 'react'
import { SiCashapp } from 'react-icons/si'
import useAxios from '@/hooks/useAxios'
import { User } from '@/types/User'
import ButtonDota from '@/components/utils/cards/ButtonDota'
import { Memberships } from '@/constants'

const GenerateQR = ({
  type,
  loading,
  createPaymentLink,
}: {
  type: Memberships
  loading: boolean
  createPaymentLink: (type: Memberships,) => void

}) => {
  const [showCoin, setShowCoin] = useState(false)
  const [disabled, setDisabled] = useState(false)
  const { data: user } = useAxios<User>({ url: 'users/me', method: 'get' })

  const _create = () => {
    try {
      setDisabled(true)
      createPaymentLink(type)
    } catch (err) {
      console.error(err)
    } finally {
      setDisabled
    }
  }

  const selectCoin = () => {
    setShowCoin(true)
  }

  useEffect(() => {
    const setMembership = async () => {
      if (!user) return
      const isActiveMembership = user.membership === type

      if (isActiveMembership) {
        setDisabled(true)
      } else {
        setDisabled(false)
      }
    }

    setMembership()
  }, [user?.membership])

  if (showCoin) {
    return (
      <div className="flex flex-col space-y-2">
        <div className="grid grid-cols-1 md:grid-cols-1 gap-x-4">
          <ButtonDota
            classNames="h-max bg-gradient-to-r !px-1 from-gray-200 to-green-300"
            disabled={false}
            onClick={_create}
            rounded='lg'
          >
            <div className="flex flex-row justify-center truncate items-center gap-4">
              <img
                src="/img/coins/USDT.svg"
                height={50}
                width={50}
                className="h-[50px] w-[50px]"
              />
              <span className='text-black text-2xl'>Tether (USDT)</span>
            </div>
          </ButtonDota>
        </div>

      </div>
    )
  }

  return (
    <div className='flex w-full'>
      <ButtonDota classNames='w-full text-base 2xl:text-2xl h-[76px] truncate hover:text-white duration-300' rounded='lg' disabled={disabled} loading={loading} onClick={selectCoin}>
        Comprar Membresia
      </ButtonDota>
    </div>
  )
}

export default GenerateQR
